export default {
	name: "",
	components: {

	},
	props: {},
	data() {
		return {
			// 接收名字
			nickname: ''
		}
	},
	methods: {
		// 输入框的输入事件
		changeVal(e) {
			this.nickname = e.detail.value
		},
		changename() {
			this.$store.state.userObj.nickname = this.nickname
			this.$changes()
		}
	},
	mounted() {
		if (uni.getStorageSync('userinfo')) {
			this.$store.state.userObj = JSON.parse(uni.getStorageSync('userinfo'))
		}
	},
	onLoad(option) {
		this.nickname = option.name
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
